@import "../../../css-config/mixins.scss";
.product-card {
  cursor: pointer;
  width: calc(50% - 5px);
  margin-bottom: 20px;
  @include md {
    width: calc(25% - 15px);
  }
  .thumbnail {
    width: 100%;
    height: 180px;
    background-color: transparent;
    margin-bottom: 30px;
    padding: 25px;
    display: flex;
    align-items: center;

    @include md {
      height: 250px;
    }

    img {
      transition: all ease 0.3s;
      display: block;
      width: 100%;
    }

    &:hover {
      img {
        transform: scale(1.2);
      }
    }
  }
  .prod-details {
    .name {
      font-size: 14px;
      display: block;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      @include md {
        font-size: 16px;
        margin-bottom: 10px;
      }
    }
    .price {
      font-size: 18px;
      @include md {
        font-size: 24px;
      }
    }
    .pricestrike {
      text-decoration: line-through;
    }
  }
}
